<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                设备共享
            </p>
        </section>
        <section class="middle">
            <md-drop-menu :data="data"/>
        </section>
        <section class="bottom">
            <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view-0" :style="{height: Height}">
                <md-scroll-view @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in list"
                            class="scroll-view-item"
                            :key="i"
                            @click="$_onItemClick(i)">
                        <label>电子信息</label>
                        <img src="@/assets/home/message_image_04@2x.png" alt="">
                        <div class="article">
                            <p>人工智能将引领新一代物流技术的发展方向</p>
                            <span><img src="@/assets/home/icon_time@2x.png" alt="">2019-07-14</span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, DropMenu, Icon } from 'mand-mobile'
export default {
  name: 'Devices',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [DropMenu.name]: DropMenu,
    [Icon.name]: Icon
  },
  data () {
    return {
      // tab项
      list: 10,
      isFinished: false,
      Height: '900px',
      data: [
        {
          text: '所属领域',
          options: [
            {
              value: '0',
              text: '1.6L'
            },
            {
              value: '1',
              text: '1.8L'
            },
            {
              value: '2',
              text: '2.0L'
            },
            {
              value: '3',
              text: '1.2T'
            },
            {
              value: '4',
              text: '1.4T'
            },
            {
              value: '5',
              text: '1.6T'
            }
          ]
        },
        {
          text: '地区选择',
          options: [
            {
              value: '7',
              text: '手动挡'
            },
            {
              value: '8',
              text: '自动挡'
            },
            {
              value: '9',
              text: '手自一体'
            }
          ]
        }]
    }
  },
  mounted () {
    console.log(window.innerHeight)
    this.Height = window.innerHeight - 90 + 'px'
  },
  methods: {
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.$refs.swiper.goto(index)
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push('/device/detail')
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      // async data
      setTimeout(() => {
        this.list += 10
        if (this.list >= 15) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }, 1000)
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    addItems () {
      this.list += 5
      // 如果把autoReflow设置为false, 需调用reflowScroller
      this.$refs.scrollView.reflowScroller()
    },
    // 回退
    goBack () {
      this.$router.push('/scienceBank')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        padding-top 88px
        height 100%
        margin-bottom 0
        .middle
            border-bottom 1px solid rgba(153, 153, 153, 0.6)
            .md-drop-menu
                height 80px
                border-bottom 1px solid #eeeeee
                .md-drop-menu-bar
                .bar-item
                        border-right 1px solid #eeeeee
        .bottom
            width 690px
            margin 0 auto
            padding-top 88px
    .md-example-child-scroll-view-0
        height 100%
        background #FFF
        .scroll-view-item
            padding  30px 0 30px 0
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 210px
            border-bottom 2px solid rgba(153, 153, 153, 0.4)
            label
                position absolute
                color #ffffff
                background:linear-gradient(0deg,rgba(202,2,57,1),rgba(255,97,53,1))
                width 100px
                height 32px
                line-height 32px
                left 8px
                font-size 20px
                font-family PingFang-SC-Regular
                border-radius 0 0 4px 4px
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    .md-drop-menu
        font-size 28px
        top 88px
        z-index 8
    /deep/ .md-drop-menu-list
        padding-top 170px!important
</style>
